  font-style: normal;
  font-display: block;
}

body {
  margin: 0;
  font-family: 'Tahoma';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
}
#root #cnct {
  color: #fff;
  font-size: 29px;
  line-height: 40px;
  font-weight: 400;
  border-radius: 50px;
  border:0;
  padding: 20px;
  width: 25vw;
  font-family: 'Tahoma';
 transition: all 0.25s ease-in-out;
}
#root #cnct:hover {
	box-shadow: none;
	color: #222223;
	background: #fff;
}
#root header #cnct {
	width: auto;
	padding: 12px 50px;
    font-size: 26px;
	margin-right: 25px;
}

#root .ButtonRegular:hover {
	box-shadow: none;
	color: #fff;
    background: #222223;
	border-color:#222223;
}
#root .dark .ButtonRegular:hover {
	box-shadow: none;
	color: #222223;
    background: #fff;
	border-color:#fff;
}
#root .ButtonRegularAlt:hover {
	box-shadow: none;
	color: #fff;
    background-color: #FBA300;
}
#root .drop_menu:focus {
    box-shadow: none;
}
body .drop_menu_wrap {
    top: 0 !important;
    max-height: 100vh !important;
    background-color: #222223;
}
