.drop_menu_wrap button {
  padding: 24px 31px;
}
@media all and (max-width:1680px) {
    #root header #cnct {
        padding: 12px 40px;
        font-size: 20px;
        line-height: 24px;
    }
    #root .top_banner_home_medium #cnct {
        font-size: 22px;
        line-height: 24px;
        padding: 18px 60px;
        width: auto;
    }
}
@media all and (max-width:1170px) {
  #root .top_banner_home_small #cnct {
    width: auto;
    padding: 18px 50px;
    font-size: 24px;
    line-height: 26px;
  }
  .drop_menu_wrap > div > .StyledBox-sc-13pk1d4-0:first-child {
    align-items: end;
  }
  .drop_menu_wrap > div > .StyledBox-sc-13pk1d4-0:last-child {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    align-items: center;
  }
  .drop_menu_wrap > div > .StyledBox-sc-13pk1d4-0:nth-child(even) {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    align-items: center;
  }
  .drop_menu_wrap .menu_link {
    color: #fff;
    font-size: 20px;
    line-height: 50px;
  }
  .drop_menu_wrap .menu_link:hover,
  .drop_menu_wrap .menu_link:focus {
    background-color: transparent;
    color: #FBA300;
  }
  .drop_menu_wrap .menu_link > div {
      padding: 0;
  }
}
@media all and (max-width:992px) {
    #root .top_banner_home_small #cnct {
        font-size: 20px;
        line-height: 22px;
        padding: 12px 10px 14px;
        width: 100%;
        max-width: 265px;
    }
    #root .mainFooter {
        display: block;
        text-align: center;
    }
}

.link { color: #FF0000; } /* CSS link color (red) */
.link:hover { color: #00FF00; } /* CSS link hover (green) */
.hover-item {
	background-color: #FFF;
}

.hover-item:hover {
	background-color: inherit;
}
